* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.cursor-p {
  cursor: pointer;
}

.mt-sm {
  margin-top: .3rem !important;
}

.mt-md {
  margin-top: 1rem !important;
}

.bblr-0 {
  border-bottom-left-radius: unset !important;
}

.bbrr-0 {
  border-bottom-right-radius: unset !important;
}

.bg-img {
  background-blend-mode: normal;
  background-image: url("wallpaper-2.b83720a5.jpg");
  background-position: bottom;
  background-repeat: no-repeat;
  background-attachment: fixed;
}

:root {
  --bg-primary: #1a1a1a;
  --bg-secondary: #333;
  --bg-ternary: #666;
  --color-primary: #e6e6e6;
  --color-secondary: #999;
}

html {
  background-color: #6c946f80;
  font-size: 62.5%;
}

body {
  background-color: var(--bg-primary);
  color: var(--color-primary);
  width: 41.2rem;
  height: 91.5rem;
  max-height: 100vh;
  margin: auto;
  font-family: Open Sans, sans-serif;
  font-size: 1.6rem;
}

.chat {
  height: 100%;
  overflow: auto;
}

.contacts {
  padding: 1rem;
}

.title {
  text-align: center;
  padding-top: 1rem;
  font-size: 2.4rem;
  font-style: oblique;
}

.messages {
  flex-direction: column;
  height: calc(100% - 13rem);
  margin-top: 7rem;
  padding: 0 1rem;
  display: flex;
  overflow: scroll;
}

.contactCard, .selected, .groupCard {
  border-radius: .5rem;
  align-items: center;
  gap: 1.5rem;
  margin-bottom: 1rem;
  padding: 1rem;
  display: flex;
}

.selected {
  background-color: var(--bg-primary);
  width: 41.2rem;
  padding: 1rem;
  position: fixed;
  top: 0;
}

.contactCard .name, .groupCard .name, .selected .name {
  font-size: 2rem;
  font-weight: 600;
}

.selected .name {
  font-weight: 500;
}

.dp {
  border-radius: 50%;
  width: 5rem;
}

.letter-dp {
  color: #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 4.5rem;
  height: 4.5rem;
  font-size: 3.2rem;
  font-weight: 800;
  display: flex;
}

.back.arrow {
  background-color: var(--color-primary);
  width: 24px;
  height: 24px;
  margin-left: 0;
  transform: rotate(180deg);
  mask-image: url("arrow.edcd58c7.svg");
  mask-position: center;
  mask-size: 100%;
  mask-repeat: no-repeat;
}

.input-box {
  font-size: inherit;
  color: inherit;
  background: var(--bg-secondary);
  border: none;
  border-radius: 10rem;
  outline: none;
  font-family: inherit;
}

.input-box::placeholder {
  color: var(--color-secondary);
}

.send-form {
  z-index: 100;
  width: 41.2rem;
  padding: 1rem 1rem 2rem;
  display: flex;
  position: fixed;
  bottom: 0;
}

.input-box.send {
  background-size: 3rem;
  width: 100%;
  padding: 1rem 7rem 1rem 2rem;
}

.send-btn {
  width: 4rem;
  margin-left: -6rem;
}

.search-form {
  padding: 1rem;
  position: sticky;
  top: 0;
}

.input-box.search {
  background-image: url("search.452f5fbe.svg");
  background-position: 6%;
  background-repeat: no-repeat;
  background-size: 2.3rem;
  width: 100%;
  margin-top: 1rem;
  padding: 1rem 2rem 1rem 6rem;
  font-size: 1.8rem;
}

.msg {
  border-radius: 1.3rem;
  width: auto;
  max-width: 80%;
  margin: 0 1rem;
  padding: .8rem 1.2rem;
}

.start-conversation {
  color: var(--color-secondary);
  margin: auto;
  font-weight: 800;
}

.sender-name {
  max-width: 100%;
  font-size: 1.2rem;
  font-weight: 800;
  display: block;
}

.sent-at {
  float: right;
  margin-top: 1rem;
  margin-left: 1rem;
  font-size: 1.2rem;
}

.msg-right {
  background-color: var(--bg-ternary);
  margin-left: auto;
}

.msg-left {
  background-color: var(--bg-secondary);
  margin-right: auto;
}

.msg:first-child {
  margin-top: auto !important;
}

.last-msg {
  color: var(--color-secondary);
  font-size: 1.4rem;
}

::-webkit-scrollbar {
  width: .5rem;
}

::-webkit-scrollbar-track {
  display: none;
}

::-webkit-scrollbar-thumb {
  background-color: var(--color-secondary);
  border-radius: 2rem;
}

::-webkit-scrollbar-thumb:hover {
  background: var(--color-secondary);
}

@media screen and (width <= 50em) {
  body {
    width: unset;
    max-height: unset;
    height: 100vh;
  }

  .send-form {
    width: 100%;
  }

  .messages {
    height: calc(100% - 15rem);
  }

  .selected {
    width: 100%;
  }
}
